import {observable, action, computed} from 'mobx';

import GoogleOAuth from '../Plugin/GoogleOAuth';

class GoogleOAuthState {
    @observable signinStatus = '';
    @observable loginBtnStr = '';
    @observable isSignedIn = false;
    @observable isRegistered = false;
    @observable userInfo = {username: '', userAddr: '', createTime: '', publicKeyBuff: null};

    @action.bound setUserInfo({username = '', userAddr = '', createTime = '', publicKeyBuff = null}) {
        this.userInfo.username = username;
        this.userInfo.userAddr = userAddr;
        this.userInfo.createTime = createTime;
        this.userInfo.publicKeyBuff = publicKeyBuff;
    }

    @action.bound onLoginSuccess() {
        this.signinStatus = 'You are currently signed in and have granted access to this app.';
        this.loginBtnStr = 'Logout';
        this.isSignedIn = true;
    }

    @action.bound onLoggedOut() {
        this.signinStatus = 'You have not authorized this app or you are signed out.';
        this.loginBtnStr = 'Login';
        this.isSignedIn = false;
    }

    @action.bound onClickOAuth() {
        GoogleOAuth.handleAuthClick();
    }

    @action.bound clearState() {
        this.signinStatus = '';
        this.loginBtnStr = 'Login';
        this.isSignedIn = false;
        this.isRegistered = false;
        this.userInfo = {username: '', userAddr: '', createTime: '', publicKeyBuff: null};
    }
}

export default GoogleOAuthState;