import {observable, action, computed} from 'mobx';

class NotificationSnackState {
    @observable open = false;
    @observable message = '';

    @action.bound triggerOpen(msg = '') {
        this.message = msg;
        this.open = true;
    }

    @action.bound handleClose() {
        this.open = false;
    };

    @action.bound showUnknownError() {
        this.triggerOpen('Sorry! An error occurred.');
    }
}

export default NotificationSnackState;