import {observable, action, computed} from 'mobx';

class CreateReplyActionState {
    @observable isModalEditorOpen = false;

    @action.bound setModalEditorOpen() {
        this.isModalEditorOpen = true;
    }

    @action.bound setModalEditorClose() {
        this.isModalEditorOpen = false;
    }
}

export default CreateReplyActionState;