import zlib from 'zlib';

class ZlibHelper {
    static gzip(unzippedData, isOutputBase64 = true) {
        let dataBuff = null;
        if (typeof unzippedData === 'string') dataBuff = Buffer.from(unzippedData, 'utf8');
        else dataBuff = unzippedData;
        return new Promise((resolve, reject) => {
            zlib.gzip(dataBuff, (err, buffer) => {
                if (err) return reject(err);
                resolve(isOutputBase64 ? buffer.toString('base64') : buffer);
            });
        });
    }

    static unzip(zippedData, isOutputUtf8 = true) {
        let dataBuff = null;
        if (typeof zippedData === 'string') dataBuff = Buffer.from(zippedData, 'base64');
        else dataBuff = zippedData;
        return new Promise((resolve, reject) => {
            zlib.unzip(dataBuff, (err, buffer) => {
                if (err) return reject(err);
                resolve(isOutputUtf8 ? buffer.toString('utf8') : buffer);
            });
        });
    }
}

export default ZlibHelper;