import React, {Component, Fragment} from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import {Link, withRouter} from 'react-router-dom';
import {inject, observer} from 'mobx-react';
import {EditorState, convertToRaw, convertFromRaw} from 'draft-js';
import {Editor} from 'react-draft-wysiwyg';

import {withStyles} from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import Zoom from '@material-ui/core/Zoom';
import Fab from '@material-ui/core/Fab';
import green from '@material-ui/core/colors/green';
import CircularProgress from '@material-ui/core/CircularProgress';

import FavoriteIcon from '@material-ui/icons/Favorite';
import ShareIcon from '@material-ui/icons/Share';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import PersonIcon from '@material-ui/icons/Person';
import ThumbUpIcon from '@material-ui/icons/ThumbUp';
import ThumbDownIcon from '@material-ui/icons/ThumbDown';
import RefreshIcon from '@material-ui/icons/Refresh';

import forumAjaxAdapter from '../Adapters/ForumAjaxAdapter';
import ZlibHelper from '../Utils/ZlibHelper';
import GoogleOAuth from "../Plugin/GoogleOAuth";

const styles = theme => ({
    gridContainer: {
        flexGrow: 1,
        marginTop: 5,
    },
    gridItem: {
        width: theme.spacing.unit * 130,
        maxWidth: '100%',
        marginBottom: 10
    },
    card: {
        padding: theme.spacing.unit * 2
    },
    cardContent: {},
    refreshBtnDiv: {
        padding: theme.spacing.unit * 3,
    },
    fabGreen: {
        color: theme.palette.common.white,
        backgroundColor: green[500],
        '&:hover': {
            backgroundColor: green[600],
        },
        display: 'flex',
        margin: '0 auto'
    },
    circularProgress: {
        display: 'block',
        margin: '0 auto'
    }
});

@inject('navState', 'threadBodyState')
@observer
class RepliesBody extends Component {
    constructor(props) {
        super(props);
        props.threadBodyState.bindUpdateThreadBodyFunc(this.updateRepliedPosts);
    }

    state = {
        replyPosts: [],
        totalCount: 0,
        finished: false
    };

    updateRepliedPosts = () => {
        const {threadAddr} = this.props;
        this.setState({finished: false});
        forumAjaxAdapter.getRepliedPostsWithUserInfo(threadAddr).then(r => {
            const promises = [];
            r.posts.forEach(val => promises.push(ZlibHelper.unzip(val.postContent)));
            Promise.all(promises).then((unzippeds) => {
                r.posts.map((val, index) => {
                    val.postContent = EditorState.createWithContent(convertFromRaw(JSON.parse(unzippeds[index])));
                    return val;
                });
                this.setState({replyPosts: r.posts, totalCount: r.totalCount, finished: true});
                // console.log(this.state)
            });
        });
    };

    componentDidMount() {
        this.updateRepliedPosts();
    }

    render() {
        const {classes} = this.props;
        return (
            <Fragment>
                {this.state.replyPosts.map((post, index) => {
                    return (<Grid key={post.postAddr} item className={classes.gridItem}>
                        <Card elevation={24} className={classes.card}>
                            <CardHeader
                                avatar={
                                    <Avatar aria-label="Recipe">
                                        <PersonIcon/>
                                    </Avatar>
                                }
                                action={'#' + (2 + index)}
                                title={post.userInfo.username}
                                subheader={post.requestTime.calendar()}
                            />
                            <CardContent className={classes.cardContent}>
                                <Editor toolbarHidden editorState={post.postContent} readOnly={true}/>
                            </CardContent>
                        </Card>
                    </Grid>);
                })}
                <Grid item className={classes.gridItem}>
                    <div className={classes.refreshBtnDiv}>
                        {this.state.finished ? (
                            <Zoom
                                key='inherit'
                                in
                                unmountOnExit
                            >
                                <Fab onClick={this.updateRepliedPosts} className={classes.fabGreen} color='inherit'>
                                    <RefreshIcon/>
                                </Fab>
                            </Zoom>
                        ) : (<CircularProgress size={56} className={classes.circularProgress}/>)}
                    </div>
                </Grid>
            </Fragment>
        );
    }
}

const RepliesBodyStyled = withStyles(styles)(RepliesBody);

@inject('navState', 'threadBodyState', 'googleOAuthState', 'notificationSnackState')
@observer
class ThreadBody extends Component {
    state = {
        authorAddr: "",
        threadAddr: "",
        threadContent: EditorState.createEmpty(),
        threadIndex: "",
        threadTitle: "",
        requestTime: "",
        authorInfo: {},
        up: 0,
        down: 0
    };

    componentWillMount() {
        const {threadBodyState} = this.props;
        threadBodyState.setThreadAddr(this.props.match.params.threadAddr);
    }

    async componentDidMount() {
        const {navState, notificationSnackState} = this.props;
        navState.setIndexTitleMode(false);
        navState.updateNavTitle();
        navState.setReplyBtnState();
        const {threadAddr} = this.props.match.params;
        try {
            const threadData = await forumAjaxAdapter.getThread(threadAddr);
            // console.log(threadData);
            navState.updateNavTitle(threadData.threadTitle);
            const unzippedContent = await ZlibHelper.unzip(threadData.threadContent);
            threadData.threadContent = EditorState.createWithContent(convertFromRaw(JSON.parse(unzippedContent)));
            this.setState(threadData);
            const authorInfo = await forumAjaxAdapter.getUserInfo(threadData.authorAddr);
            this.setState({authorInfo: authorInfo});
            const totalVoted = await forumAjaxAdapter.getTotalVoted(threadAddr);
            this.setState({up: totalVoted.up, down: totalVoted.down});
        } catch (e) {
            notificationSnackState.showUnknownError();
        }
    }

    componentWillUnmount() {
        const {navState} = this.props;
        navState.updateNavTitle();
        navState.setIndexTitleMode(true);
        navState.clearCreateReplyBtnState();
    }

    submitVote = async (vote, updown) => {
        const {googleOAuthState, notificationSnackState} = this.props;
        if (!googleOAuthState.isRegistered) return notificationSnackState.triggerOpen('Please login first!');
        try {
            const {userAddr} = GoogleOAuth.getUserInfo();
            const {threadAddr} = this.props.match.params;
            const time = new Date().getTime().toString();
            const signBase64 = GoogleOAuth.signSignature(Buffer.concat([
                Buffer.from(userAddr, 'utf8'),
                Buffer.from(threadAddr, 'utf8'),
                Buffer.from(vote, 'utf8'),
                Buffer.from(time, 'utf8'),
            ]));
            await forumAjaxAdapter.submitNewVote(userAddr, threadAddr, vote, time, signBase64)
                .then(() => {
                    this.setState({[updown]: this.state[updown] + 1});
                    notificationSnackState.triggerOpen('Vote the thread successfully!');
                });
        } catch (e) {
            notificationSnackState.showUnknownError();
        }
    };

    submitUpVote = () => {
        this.submitVote('1', 'up');
    };

    submitDownVote = () => {
        this.submitVote('-1', 'down');
    };

    render() {
        const {classes} = this.props;
        return (
            <Grid container direction="column" justify="center" alignItems="center"
                  className={classes.gridContainer}>
                <Grid item className={classes.gridItem}>
                    <Card className={classes.card} elevation={24}>
                        <CardHeader
                            avatar={
                                <Avatar aria-label="Recipe">
                                    <PersonIcon/>
                                </Avatar>
                            }
                            action="#1"
                            title={this.state.authorInfo.username}
                            subheader={this.state.requestTime ? this.state.requestTime.calendar() : this.state.requestTime}
                        />
                        <CardContent className={classes.cardContent}>
                            <Editor toolbarHidden editorState={this.state.threadContent} readOnly={true}/>
                        </CardContent>
                        <CardActions className={classes.actions} disableActionSpacing>
                            <IconButton onClick={this.submitUpVote} aria-label="Vote Up">
                                <ThumbUpIcon/>
                            </IconButton>
                            {this.state.up}
                            <IconButton onClick={this.submitDownVote} aria-label="Vote Down">
                                <ThumbDownIcon/>
                            </IconButton>
                            {this.state.down}
                        </CardActions>
                    </Card>
                </Grid>
                <RepliesBodyStyled threadAddr={this.props.match.params.threadAddr}/>
            </Grid>
        );
    }
}

export default withStyles(styles)(ThreadBody);

