import React, {Component} from 'react';
import {inject, observer} from 'mobx-react';
import {Redirect} from "react-router-dom";

import './App.css';

import GoogleOAuth from './Plugin/GoogleOAuth';
import forumAjaxAdapter from './Adapters/ForumAjaxAdapter';

@inject('googleOAuthState')
class App extends Component {

    state = {
        isRedirectRegistration: false
    };

    componentWillMount() {
        const googleOAuthState = this.props.googleOAuthState;
        GoogleOAuth.init((isPass) => {
            if (isPass) {
                googleOAuthState.onLoginSuccess();
                GoogleOAuth.userPrepare().then((isRegistered) => {
                    // console.log(isRegistered);
                    // console.log(GoogleOAuth.isRegistered());
                    // console.log(GoogleOAuth.getUserInfo());

                    // googleOAuthState.isRegistered = isRegistered;
                    // googleOAuthState.setUserInfo(GoogleOAuth.getUserInfo());
                    // this.setState({isRedirectRegistration: !isRegistered});

                    if (isRegistered) {
                        // googleOAuthState.isRegistered = isRegistered;
                        // googleOAuthState.setUserInfo(GoogleOAuth.getUserInfo());
                        // this.setState({isRedirectRegistration: !isRegistered});
                        forumAjaxAdapter.getUserInfo(GoogleOAuth.getUserInfo().userAddr)
                            .then(() => {
                                googleOAuthState.isRegistered = isRegistered;
                                googleOAuthState.setUserInfo(GoogleOAuth.getUserInfo());
                                this.setState({isRedirectRegistration: !isRegistered});
                            })
                            .catch((e) => {
                                if (e.response.data.error.message === 'Tx not found') {
                                    googleOAuthState.isRegistered = false;
                                    this.setState({isRedirectRegistration: true});
                                }
                            });
                    } else {
                        googleOAuthState.isRegistered = isRegistered;
                        this.setState({isRedirectRegistration: !isRegistered});
                    }

                });
            } else {
                googleOAuthState.onLoggedOut();
            }
        });
    }

    render() {
        const {classes} = this.props;
        if (this.state.isRedirectRegistration) {
            return <Redirect to="/registration"/>;
        }
        return '';
    }
}

export default App;
