import React, {Component} from 'react';
import {inject, observer} from 'mobx-react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import {Redirect} from 'react-router-dom';

import {Card, withStyles} from '@material-ui/core';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import CardContent from "@material-ui/core/CardContent";
import TextField from '@material-ui/core/TextField';
import Avatar from '@material-ui/core/Avatar';
import PersonIcon from '@material-ui/icons/Person';
import FacetIcon from '@material-ui/icons/Face';
import CardHeader from "@material-ui/core/CardHeader";

import moment from "moment";
import crypto from "crypto";

const styles = theme => ({
    paper: {
        padding: theme.spacing.unit * 5,
        // marginTop: theme.spacing.unit * 2,
        width: theme.spacing.unit * 100,
        maxWidth: '100%',
        maxHeight: '100%',
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        maxWidth: '97%'
    }
});


@inject('googleOAuthState')
@observer
class Profile extends Component {
    render() {
        const {classes, googleOAuthState} = this.props;
        if (!googleOAuthState.isRegistered) {
            return <Redirect to="/"/>;
        }
        const createDay = googleOAuthState.userInfo.createTime ? moment(parseInt(googleOAuthState.userInfo.createTime)).format("Do MMM YYYY") : '';
        const userId = googleOAuthState.userInfo.publicKeyBuff ? crypto.createHash('sha256').update(googleOAuthState.userInfo.publicKeyBuff).digest('hex') : '';
        return (
            <Grid container justify="center" alignItems="center">
                <Card className={classes.paper} square elevation={10}>
                    <Typography variant="h6" component="h6" align="center">
                        <FacetIcon style={{verticalAlign: 'sub'}}/> Profile
                    </Typography>
                    <Divider/>
                    <CardHeader
                        avatar={
                            <Avatar aria-label="Recipe">
                                <PersonIcon/>
                            </Avatar>
                        }
                        title={googleOAuthState.userInfo.username}
                        subheader={'Registered on: ' + createDay}
                    />
                    <CardContent>
                        <TextField
                            label="User Address"
                            className={classes.textField}
                            value={googleOAuthState.userInfo.userAddr}
                            fullWidth
                            margin="normal"
                            variant="filled"
                            disabled
                        />
                        <TextField
                            label="User ID"
                            className={classes.textField}
                            value={userId}
                            fullWidth
                            margin="normal"
                            variant="filled"
                            disabled
                        />
                    </CardContent>
                </Card>
            </Grid>
        );
    }
}

export default withStyles(styles)(Profile);