import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {inject, observer} from 'mobx-react';

import {withStyles} from "@material-ui/core";
import Typography from '@material-ui/core/Typography';
import Modal from '@material-ui/core/Modal';
import Paper from '@material-ui/core/Paper';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';

import WysiwygEditor from "./WysiwygEditor";

const styles = theme => ({
    paper: {
        position: 'absolute',
        width: theme.spacing.unit * 100,
        maxWidth: '100%',
        minHeight: theme.spacing.unit * 60,
        maxHeight: '90%',
        backgroundColor: theme.palette.background.paper,
        boxShadow: theme.shadows[5],
        padding: theme.spacing.unit * 4,
        outline: 'none',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
    },
    sendIcon: {
        marginLeft: theme.spacing.unit
    },
    closeBtn: {
        float: 'right'
    }
});


@inject('navState', 'createReplyActionState')
@observer
class CreateReplyModal extends Component {
    render() {
        const {classes, navState, createReplyActionState} = this.props;
        return (
            <Modal
                aria-labelledby="simple-modal-title"
                aria-describedby="simple-modal-description"
                open={createReplyActionState.isModalEditorOpen}
                onClose={createReplyActionState.setModalEditorClose}
            >
                <Paper elevation={1} square className={classes.paper}>
                    <IconButton className={classes.closeBtn} onClick={createReplyActionState.setModalEditorClose}>
                        <CloseIcon/>
                    </IconButton>
                    <Typography variant="h6" id="modal-title">
                        {navState.getModalEditorTitle}
                    </Typography>
                    <WysiwygEditor/>
                </Paper>
            </Modal>
        );
    }
}

export default withStyles(styles)(CreateReplyModal);