import * as serviceWorker from './serviceWorker';
import React, {Fragment} from 'react';
import {Router, Route, Switch, Link} from 'react-router-dom'
import ReactDOM from 'react-dom';
import {Provider} from 'mobx-react';
import {createBrowserHistory} from 'history'
import "antd/dist/antd.css";

import NavState from './Stores/NavState';
import GoogleOAuthState from './Stores/GoogleOAuthState';
import CreateReplyActionState from './Stores/CreateReplyActionState';
import ThreadListState from './Stores/ThreadListState';
import ThreadBodyState from './Stores/ThreadBodyState';
import NotificationSnackState from './Stores/NotificationSnackState';


import './index.css';
import Nav from './Layout/Nav';
import App from './App';
import ThreadList from './Contents/ThreadList';
import ThreadBody from './Contents/ThreadBody';
import Registration from './Contents/Registration';
import CreateReplyModalCombo from './Components/CreateReplyModalCombo';
import NotificationSnack from './Components/NotificationSnack';
import About from './Contents/About';
import Terms from './Contents/Terms';
import Profile from './Contents/Profile';

const stores = {
    navState: new NavState(),
    googleOAuthState: new GoogleOAuthState(),
    createReplyActionState: new CreateReplyActionState(),
    threadListState: new ThreadListState(),
    threadBodyState: new ThreadBodyState(),
    notificationSnackState: new NotificationSnackState()
};
const history = createBrowserHistory({});


class SS extends React.Component {

    render() {
        const {classes} = this.props;
        return (
            <div>
                <CreateReplyModalCombo/>
            </div>
        );
    }
}


ReactDOM.render((
    <Provider {...stores}>
        <Router history={history}>
            <Fragment>
                <App/>
                <Nav/>
                <NotificationSnack/>
                <Switch>
                    <Route path="/" exact component={ThreadList}/>
                    <Route path="/about" component={About}/>
                    <Route path="/profile" component={Profile}/>
                    <Route path="/terms" component={Terms}/>
                    <Route path="/registration" component={Registration}/>
                    <Route path="/thread/:threadAddr" component={ThreadBody}/>
                    <Route path="/SS" component={SS}/>
                </Switch>
            </Fragment>
        </Router>
    </Provider>
), document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();
