import React from 'react';
import PropTypes from 'prop-types';
import {inject, observer} from 'mobx-react';
import {withRouter, Link} from "react-router-dom";

import {withStyles} from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import {blue, pink} from '@material-ui/core/colors';
import MuiThemeProvider from '@material-ui/core/styles/MuiThemeProvider';
import createMuiTheme from '@material-ui/core/styles/createMuiTheme';
import Grid from '@material-ui/core/Grid';
import Fab from '@material-ui/core/Fab';
import AddIcon from '@material-ui/icons/Add';
import ReplyIcon from '@material-ui/icons/Reply';
import Zoom from '@material-ui/core/Zoom';

import TempDrawer from './TempDrawer';
import logo from '../Icons/logox.svg';
import CreateReplyModalCombo from '../Components/CreateReplyModalCombo';

const muiThemeX = createMuiTheme({
    palette: {
        primary: blue
    }
});

const styles = theme => ({
    appBar: {
        color: pink,
    },
    root: {
        flexGrow: 1,
    },
    grow: {
        flexGrow: 1,
        display: 'inline-block',
        verticalAlign: 'middle'
    },
    menuButton: {
        marginLeft: -12,
        // marginRight: 20,
    },
    fab: {
        position: 'fixed',
        margin: theme.spacing.unit,
        bottom: theme.spacing.unit * 3,
        right: theme.spacing.unit * 1,
        zIndex: 1000
    }
});

@inject('googleOAuthState', 'navState', 'createReplyActionState', 'threadListState')
@observer
class ButtonAppBar extends React.Component {
    state = {};

    componentDidMount() {

    }

    render() {
        const {classes, navState, googleOAuthState, createReplyActionState, threadListState} = this.props;
        let titlePart = (
            <Grid item>
                <Typography variant="h6" color="inherit" className={classes.grow}>
                    {navState.navTitle}
                </Typography>
            </Grid>
        );
        if (!navState.indexTitleMode) titlePart = (
            <Grid item xs={9}>
                <IconButton color="inherit" style={{marginRight: 10}} onClick={this.props.history.goBack}>
                    <ArrowBackIcon/>
                </IconButton>
                <Typography variant="h6" color="inherit" className={classes.grow}>
                    {navState.navTitle}
                </Typography>
            </Grid>
        );
        let createReplyBtn = '';
        if (googleOAuthState.isRegistered && navState.createReplyBtnState === 'add') {
            createReplyBtn = (
                <Zoom
                    key='primary'
                    in={true}
                    unmountOnExit
                >
                    <Fab color="primary" size='medium' aria-label="Add" className={classes.fab}
                         onClick={createReplyActionState.setModalEditorOpen}>
                        <AddIcon/>
                    </Fab>
                </Zoom>
            );
        } else if (googleOAuthState.isRegistered && navState.createReplyBtnState === 'reply') {
            createReplyBtn = (
                <Zoom
                    key='secondary'
                    in={true}
                    unmountOnExit
                >
                    <Fab color="secondary" size='medium' aria-label="Reply" className={classes.fab}
                         onClick={createReplyActionState.setModalEditorOpen}>
                        <ReplyIcon/>
                    </Fab>
                </Zoom>
            );
        }
        return (
            <MuiThemeProvider theme={muiThemeX}>
                <div className={classes.root}>
                    <AppBar position="static" elevation={10} className={classes.appBar}>
                        <Toolbar>
                            <Grid container spacing={0} justify="space-between" alignItems="center">
                                <Grid item>
                                    <TempDrawer/>
                                </Grid>
                                {titlePart}
                                <Grid item>
                                    <Link onClick={threadListState.callUpdateThreadList} to='/'><img src={logo} style={{
                                        maxWidth: '45px',
                                        verticalAlign: 'middle'
                                    }}
                                                                                                     alt="logo"/></Link>
                                </Grid>
                            </Grid>
                        </Toolbar>
                    </AppBar>
                    {createReplyBtn}
                </div>
                <CreateReplyModalCombo/>
            </MuiThemeProvider>
        );
    }
}

ButtonAppBar.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withRouter(withStyles(styles)(ButtonAppBar));
