import React, {Component} from 'react';

import {withStyles} from '@material-ui/core';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';

const styles = theme => ({
    paper: {
        padding: theme.spacing.unit * 3,
        // marginTop: theme.spacing.unit * 2,
        width: theme.spacing.unit * 140,
        maxWidth: '100%',
        maxHeight: '100%',
    }
});

class Terms extends Component {
    render() {
        const {classes} = this.props;
        return (
            <Grid container justify="center" alignItems="center">
                <Paper className={classes.paper} square elevation={10}>
                    <Typography variant="h4" component="h4" align="center">
                        Terms of Service and Disclaimer
                    </Typography>
                    <Divider/>
                    <Typography paragraph component="p"/>
                    <Typography paragraph component="p"/>

                    <ol style={{fontWeight: 'bold'}}>
                        <li>
                            <Typography paragraph component="p">
                                <strong>Acceptance of Terms</strong>
                            </Typography>
                            <Typography paragraph component="p">
                                Welcome to the forum <a href="http://fyp.nathanlam11.tk">fyp.nathanlam11.tk</a>. It is
                                also under the domain name <strong>nathanlam11.tk</strong> by Nathan Lam.
                                This forum is a part of university final year project created by Nathan Lam
                                which provides the service only for academic and evaluation purpose.
                                The following are the terms and conditions for the use of this Forum.
                            </Typography>
                        </li>
                        <li>
                            <Typography paragraph component="p">
                                <strong>Description of use and service</strong>
                            </Typography>
                            <Typography paragraph component="p">
                                This forum is providing user with discussion through the Internet.
                                You understand and agree that the service is only for academic and evaluation purpose.
                                You also understand and agree that the service is provided to you on an "as is"
                                basis. <a href="http://fyp.nathanlam11.tk">fyp.nathanlam11.tk</a> disclaims
                                any liability as a result of any data deletion,
                                interruption or any failures or personalized settings. <a
                                href="http://fyp.nathanlam11.tk">fyp.nathanlam11.tk</a> reserves
                                the right to without giving any prior notice to you deactivate any or all of the service
                                (including user account, discussion threads and reply posts etc.)
                                to maintain forum working normally that <a
                                href="http://fyp.nathanlam11.tk">fyp.nathanlam11.tk</a> thinks appropriate.
                            </Typography>
                        </li>
                        <li>
                            <Typography paragraph component="p">
                                <strong>Registration obligation of user</strong>
                            </Typography>
                            <Typography paragraph component="p">
                                In order to take part in the discussion on the forum, you agree to authorize your
                                Google account and allow <a href="http://fyp.nathanlam11.tk">fyp.nathanlam11.tk</a> to
                                access your
                                Google drive <strong>only for storing your registration information</strong> through
                                Google OAuth and Google APIs. <a
                                href="http://fyp.nathanlam11.tk">fyp.nathanlam11.tk</a> disclaims
                                any liability as a result of <strong>loss</strong> or leak your registration information
                                because of improper user's operation or Google's policy.
                            </Typography>
                        </li>
                        <li>
                            <Typography paragraph component="p">
                                <strong>User behavior</strong>
                            </Typography>
                            <Typography paragraph component="p">
                                You understand and confirm that the third party content providers may be solely
                                responsible for their
                                information data, video, music, sound, graphics, photographs, text, news or other
                                contents are posted publicly or
                                transmitted privately and <a
                                href="http://fyp.nathanlam11.tk">fyp.nathanlam11.tk</a> takes no responsibility for
                                those contents.
                                You agree and undertake not to use the service for any of illegal, damaging, threatening
                                and abusing
                                privacy infringing, obscene, immoral, discriminating, indecent.
                                <a href="http://fyp.nathanlam11.tk">fyp.nathanlam11.tk</a> reserves the right to
                                deactivate the user who are violate terms of use.
                            </Typography>
                        </li>
                    </ol>
                </Paper>
            </Grid>
        );
    }
}

export default withStyles(styles)(Terms);