import {observable, action, computed} from 'mobx';

class NavState {
    @observable navTitle = '';
    @observable indexTitleMode = true;
    @observable createReplyBtnState = '';

    @action.bound updateNavTitle(title = '') {
        this.navTitle = title;
    }

    @action.bound setIndexTitleMode(bool) {
        this.indexTitleMode = bool;
    }

    @action.bound setCreateBtnState() {
        this.createReplyBtnState = 'add';
    }

    @action.bound setReplyBtnState() {
        this.createReplyBtnState = 'reply';
    }

    @action.bound clearCreateReplyBtnState() {
        this.createReplyBtnState = '';
    }

    @action.bound createReplyBtnClick() {
        console.log('createReplyBtnClick');
    }

    @computed get getModalEditorTitle() {
        if (this.createReplyBtnState === 'add') return 'New Thread:';
        else if (this.createReplyBtnState === 'reply') return 'New Reply:';
        return '';
    }

    @computed get getModalEditorBtnTxt() {
        if (this.createReplyBtnState === 'add') return 'Create';
        else if (this.createReplyBtnState === 'reply') return 'Reply';
        return '';
    }
}

export default NavState;