import React from 'react';
import PropTypes from 'prop-types';

import {inject, observer} from 'mobx-react';
import {Link} from "react-router-dom";

import {withStyles} from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Grid from '@material-ui/core/Grid';
import PowerSettingsNewIcon from '@material-ui/icons/PowerSettingsNew';
import AccountBoxIcon from '@material-ui/icons/AccountBox';
import HelpIcon from '@material-ui/icons/Help';
import InfoIcon from '@material-ui/icons/Info';

import '../Styles/GoogleOAuthStyle.css';
import Typography from '@material-ui/core/Typography';

import GoogleOAuth from '../Plugin/GoogleOAuth';

const styles = {
    list: {
        width: 250,
    },
    fullList: {
        width: 'auto',
    },
    menuButton: {
        marginLeft: -12,
    },
};

@inject('googleOAuthState')
@observer
class LoginButton extends React.Component {

    handleOAuthLoginOut = () => {
        const {googleOAuthState} = this.props;
        GoogleOAuth.clearState();
        googleOAuthState.clearState();
        googleOAuthState.onClickOAuth();
    };

    render() {
        const {googleOAuthState} = this.props;
        if (googleOAuthState.loginBtnStr === 'Logout') {
            return <Button variant="contained" color="secondary" onClick={this.handleOAuthLoginOut}>
                <PowerSettingsNewIcon/>
                {googleOAuthState.loginBtnStr}
            </Button>;
        }
        if (googleOAuthState.loginBtnStr === 'Login') {
            return <Button style={{textTransform: "none"}} onClick={this.handleOAuthLoginOut} className="google-button">
  <span className="google-button__icon">
    <svg viewBox="0 0 366 372" xmlns="http://www.w3.org/2000/svg">
        <path
            d="M125.9 10.2c40.2-13.9 85.3-13.6 125.3 1.1 22.2 8.2 42.5 21 59.9 37.1-5.8 6.3-12.1 12.2-18.1 18.3l-34.2 34.2c-11.3-10.8-25.1-19-40.1-23.6-17.6-5.3-36.6-6.1-54.6-2.2-21 4.5-40.5 15.5-55.6 30.9-12.2 12.3-21.4 27.5-27 43.9-20.3-15.8-40.6-31.5-61-47.3 21.5-43 60.1-76.9 105.4-92.4z"
            id="Shape" fill="#EA4335"/>
        <path
            d="M20.6 102.4c20.3 15.8 40.6 31.5 61 47.3-8 23.3-8 49.2 0 72.4-20.3 15.8-40.6 31.6-60.9 47.3C1.9 232.7-3.8 189.6 4.4 149.2c3.3-16.2 8.7-32 16.2-46.8z"
            id="Shape" fill="#FBBC05"/>
        <path
            d="M361.7 151.1c5.8 32.7 4.5 66.8-4.7 98.8-8.5 29.3-24.6 56.5-47.1 77.2l-59.1-45.9c19.5-13.1 33.3-34.3 37.2-57.5H186.6c.1-24.2.1-48.4.1-72.6h175z"
            id="Shape" fill="#4285F4"/>
        <path
            d="M81.4 222.2c7.8 22.9 22.8 43.2 42.6 57.1 12.4 8.7 26.6 14.9 41.4 17.9 14.6 3 29.7 2.6 44.4.1 14.6-2.6 28.7-7.9 41-16.2l59.1 45.9c-21.3 19.7-48 33.1-76.2 39.6-31.2 7.1-64.2 7.3-95.2-1-24.6-6.5-47.7-18.2-67.6-34.1-20.9-16.6-38.3-38-50.4-62 20.3-15.7 40.6-31.5 60.9-47.3z"
            fill="#34A853"/>
        </svg>
  </span>
                <span className="google-button__text">Sign in with Google</span>
            </Button>;
        }
        return '';
    }
}

@inject('googleOAuthState')
@observer
class TemporaryDrawer extends React.Component {
    state = {
        isOpened: this.props.isOpened
    };

    toggleDrawer = (open) => () => {
        this.setState({
            isOpened: open,
        });
    };

    render() {
        const {classes, googleOAuthState} = this.props;
        const showProfileItem = googleOAuthState.isRegistered ? (
            <ListItem button key="Profile" component={props => <Link to="/profile" {...props} />}>
                <ListItemIcon><AccountBoxIcon/></ListItemIcon>
                <ListItemText primary="Profile"/>
            </ListItem>
        ) : '';
        return (
            <div>
                <IconButton className={classes.menuButton} onClick={this.toggleDrawer(true)} color="inherit"
                            aria-label="Menu">
                    <MenuIcon/>
                </IconButton>
                <Drawer open={this.state.isOpened} onClose={this.toggleDrawer(false)}>
                    <div
                        tabIndex={0}
                        role="button"
                        onClick={this.toggleDrawer(false)}
                        onKeyDown={this.toggleDrawer(false)}
                    >
                        <div className={classes.list}>
                            <List>
                                {showProfileItem}
                                <ListItem button key="About" component={props => <Link to="/about" {...props} />}>
                                    <ListItemIcon><HelpIcon/></ListItemIcon>
                                    <ListItemText primary="About"/>
                                </ListItem>
                                <ListItem button key="Terms of Service"
                                          component={props => <Link to="/terms" {...props} />}>
                                    <ListItemIcon><InfoIcon/></ListItemIcon>
                                    <ListItemText primary="Terms of Service"/>
                                </ListItem>
                            </List>
                        </div>
                    </div>
                    <Divider/>
                    <Grid container direction="column" justify="space-between" alignItems="center"
                          style={{padding: 20}}>
                        <Grid item>
                            <LoginButton/>
                        </Grid>
                        <Grid item style={{position: 'absolute', bottom: '3%'}}>
                            <Typography component="p" color="textSecondary" align="center">
                                © 2019 fyp.nathanlam11.tk.
                            </Typography>
                            <Typography component="p" color="textSecondary" align="center">
                                All rights reserved.
                            </Typography>
                        </Grid>
                    </Grid>
                </Drawer>
            </div>
        );
    }
}

TemporaryDrawer.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(TemporaryDrawer);
