const axios = require('axios');
const moment = require('moment');

class ForumAjaxAdapter {
    constructor(host = 'localhost', port = '', useHttps = false) {
        this.baseURL = `${useHttps ? 'https' : 'http'}://${host}${port ? ':' + port : port}/`;
        if (useHttps) this.baseURL = `https://${host}/`;
    }

    async getRecentThreads() {
        return (await axios.get(this.baseURL + 'api/recent-threads')).data;
    }

    async getThread(threadAddr) {
        const queryParams = {threadAddr: threadAddr};
        const data = (await axios.get(this.baseURL + 'api/thread', {params: queryParams})).data;
        data.requestTime = moment(parseInt(data.requestTime));
        return data;
    }

    async getUserInfo(userAddr) {
        const queryParams = {userAddr: userAddr};
        return (await axios.get(this.baseURL + 'api/user-info', {params: queryParams})).data;
    }

    async getRepliedPosts(threadAddr, page = 1, perPage = 100) {
        const queryParams = {threadAddr: threadAddr, page: page, perPage: perPage};
        const data = (await axios.get(this.baseURL + 'api/replied-posts', {params: queryParams})).data;
        data.posts.map((val) => {
            val.requestTime = moment(parseInt(val.requestTime));
            return val;
        });
        return data;
    }

    async getRepliedPostsWithUserInfo(threadAddr, page = 1, perPage = 100) {
        const res = (await this.getRepliedPosts(threadAddr, page, perPage));
        if (!res) {
            return {};
        }
        const allPromises = [];
        res.posts.forEach((post) => {
            allPromises.push(this.getUserInfo(post.userAddr));
        });
        const userInfos = await Promise.all(allPromises);
        res.posts.map((val, index) => {
            val.userInfo = userInfos[index];
            delete val.userInfo.publicKeyBase64;
            return val;
        });
        return res;
    }

    async getTotalVoted(threadAddr) {
        const queryParams = {threadAddr: threadAddr};
        return (await axios.get(this.baseURL + 'api/total-voted', {params: queryParams})).data;
    }

    submitNewUserContract(username, publicKey, requestTime, signature) {
        return axios.post(this.baseURL + 'api/new-user-contract', {username, publicKey, requestTime, signature});
    }

    submitNewThread(authorAddr, threadTitle, threadContent, requestTime, signature) {
        return axios.post(this.baseURL + 'api/new-thread', {
            authorAddr,
            threadTitle,
            threadContent,
            requestTime,
            signature
        });
    }

    submitNewReply(userAddr, threadAddr, postContent, requestTime, signature) {
        return axios.post(this.baseURL + 'api/new-reply-post', {
            userAddr,
            threadAddr,
            postContent,
            requestTime,
            signature
        });
    }

    submitNewVote(userAddr, threadAddr, vote, requestTime, signature) {
        return axios.post(this.baseURL + 'api/new-vote', {
            userAddr,
            threadAddr,
            vote,
            requestTime,
            signature
        });
    }
}

export default new ForumAjaxAdapter('vv.nathanlam11.tk/fyp', '', true);
