import React, {Component} from 'react';
import {inject, observer} from 'mobx-react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import {Link, Redirect} from 'react-router-dom';

import {Button, withStyles} from '@material-ui/core';
import SendIcon from '@material-ui/icons/Send';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Divider from '@material-ui/core/Divider';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';

import GoogleOAuth from '../Plugin/GoogleOAuth';
import forumAjaxAdapter from '../Adapters/ForumAjaxAdapter';

const styles = theme => ({
    gridContainer: {
        flexGrow: 1,
        marginTop: 20,
    },
    gridItem: {
        height: '40%',
        width: '40%',
        marginBottom: 10
    },
    paper: {
        ...theme.mixins.gutters(),
        paddingTop: theme.spacing.unit * 2,
        paddingBottom: theme.spacing.unit * 2,
    },
    container: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
    },
    checkbox: {
        width: '100%',
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
    },
    submitBtn: {
        margin: '0 auto'
    },
    sendIcon: {
        marginLeft: theme.spacing.unit
    }
});

@inject('navState', 'googleOAuthState', 'notificationSnackState')
class Registration extends Component {
    state = {
        aggered: false,
        inputUsername: '',
        inputError: false,
        disableBtn: false,
        disableCheckbox: false,
        disableInput: false
    };

    componentDidMount() {
        const {navState, googleOAuthState, notificationSnackState} = this.props;
        navState.updateNavTitle();
        if (googleOAuthState.isSignedIn && !googleOAuthState.isRegistered) {
            notificationSnackState.triggerOpen('Please complete the registration. Thanks.');
        }
    }

    inputUsernameChange = event => {
        this.setState({inputUsername: event.target.value, inputError: false});
    };

    checkboxChange = event => {
        this.setState({aggered: event.target.checked});
    };

    handleBtnSubmit = async event => {
        const {history, googleOAuthState, notificationSnackState} = this.props;
        if (this.state.inputUsername) {
            event.preventDefault();
        }

        const username = this.state.inputUsername.trim();
        if (!username) {
            this.setState({inputError: true});
            notificationSnackState.triggerOpen('Username cannot contain space only!');
            return;
        }
        this.setState({disableBtn: true, disableCheckbox: true, disableInput: true});

        const keyPair = GoogleOAuth.createKeyPair();
        const publicKeyBase64 = Buffer.from(keyPair.publicKey).toString('base64');
        const date = new Date().getTime().toString();
        const buf = Buffer.concat([
            Buffer.from(username, 'utf8'),
            Buffer.from(keyPair.publicKey),
            Buffer.from(date, 'utf8')
        ]);
        const signBase64 = GoogleOAuth.signSignature(buf, keyPair.secretKey);

        // const checkOAuth = await GoogleOAuth.userPrepare();
        // if (!checkOAuth) {
        const ucRes = await forumAjaxAdapter.submitNewUserContract(username, publicKeyBase64, date, signBase64);
        console.log(ucRes);
        await GoogleOAuth.register(username, keyPair, date, ucRes.data.hash);
        googleOAuthState.isRegistered = true;
        googleOAuthState.setUserInfo(GoogleOAuth.getUserInfo());
        notificationSnackState.triggerOpen('Registration succeeded!');
        // }
        history.push('/');
    };

    render() {
        const {classes, googleOAuthState} = this.props;
        if (!googleOAuthState.isSignedIn || googleOAuthState.isRegistered) {
            return <Redirect to="/"/>;
        }
        return (
            <Grid container direction="column" justify="center" alignItems="center"
                  className={classes.gridContainer}>
                <Grid item className={classes.gridItem}>
                    <Paper elevation={24} square={true} className={classes.paper}>
                        <Typography variant="h5" align="center">
                            Registration
                        </Typography>
                        <Divider style={{marginTop: '2%'}}/>
                        <form action="#" className={classes.container} autoComplete="off">
                            <TextField
                                label="Enter your username"
                                error={this.state.inputError}
                                className={classes.textField}
                                disabled={this.state.disableInput}
                                margin="normal"
                                variant="outlined"
                                type="text"
                                fullWidth
                                required
                                onChange={this.inputUsernameChange}
                            />

                            <FormControlLabel className={classes.checkbox}
                                              control={
                                                  <Checkbox
                                                      // checked={this.state.checkedA}
                                                      onChange={this.checkboxChange}
                                                      disabled={this.state.disableCheckbox}
                                                  />
                                              }
                                              label={<div>I accept and agree to the <Link to={'/terms'}>Term of
                                                  Service</Link>.</div>}
                            />

                            <Button disabled={!this.state.aggered || this.state.disableBtn} type="submit"
                                    variant="contained"
                                    className={classes.submitBtn}
                                    onClick={this.handleBtnSubmit}
                                    color="primary">
                                Continue
                                <SendIcon className={classes.sendIcon}/>
                            </Button>
                        </form>
                    </Paper>
                </Grid>
            </Grid>
        );
    }
}

export default withStyles(styles)(Registration);
