const gapi = window.gapi;

const nacl = require('tweetnacl');

const configFileName = 'config.json';
let GoogleAuth = null;
let configFileId = '';
let userInfo = {
    username: '',
    userAddr: '',
    createTime: '',
    publicKeyBuff: null
};
let publicKeyBuff = null, privateKeyBuff = null;
let publicKeyBase64 = '', privateKeyBase64 = '';
let _isRegistered = false;

function init(setSignedInState) {
    gapi.load('client:auth2', () => {
        gapi.client.init({
            'discoveryDocs': ['https://www.googleapis.com/discovery/v1/apis/drive/v3/rest'],
            'clientId': '559605306816-6kkqs7b13i832b96t3qsj50h97jlh1sc.apps.googleusercontent.com',
            'scope': 'https://www.googleapis.com/auth/drive.appdata'
        }).then(() => {
            GoogleAuth = gapi.auth2.getAuthInstance();

            // Listen for sign-in state changes.
            GoogleAuth.isSignedIn.listen((isSignedIn) => {
                setSignedInState(isSignedIn && isAuthorized());
            });

            setSignedInState(isSignedIn() && isAuthorized());
        });
    });
}

function isSignedIn() {
    if (!GoogleAuth) {
        return false;
    }
    return GoogleAuth.isSignedIn.get();
}

function isAuthorized() {
    const user = GoogleAuth.currentUser.get();
    return user.hasGrantedScopes('https://www.googleapis.com/auth/drive.appdata');
}

function handleAuthClick() {
    if (GoogleAuth.isSignedIn.get()) {
        // User is authorized and has clicked 'Sign out' button.
        GoogleAuth.signOut();
    } else {
        // User is not signed in. Start Google auth flow.
        GoogleAuth.signIn();
    }
}

////--------------------------------nacl
function createKeyPair() {
    return nacl.sign.keyPair();
}

function signSignature(message, optionPrivateKeyBuff) {
    let msgBuff = message;
    if (typeof msgBuff === 'string') {
        msgBuff = Buffer.from(message, 'utf8');
    }
    const signedBuff = nacl.sign.detached(msgBuff, optionPrivateKeyBuff || privateKeyBuff);
    return Buffer.from(signedBuff).toString('base64');
}

////--------------------------------nacl

function checkConfig(data) {
    const configProperties = ['publicKey', 'secretKey', 'createTime'];
    // const configProperties = ['username', 'publicKey', 'secretKey', 'createTime', 'userAddr'];
    return configProperties.some((val) => {
        if (!data[val]) {
            return true;
        }
    });
}


function getConfigId() {
    const listFunc = gapi.client.drive.files.list({
        'spaces': 'appDataFolder',
        'fields': 'files(id, name)'
    });
    return new Promise((resolve, reject) => {
        listFunc.execute((response) => {
            if (response.files.length > 0) return resolve(response.files[0].id);
            resolve();
        });
    });
}

function getConfigById(id) {
    const getFunt = gapi.client.drive.files.get({
        'fileId': id,
        'alt': 'media'
    });
    return new Promise((resolve, reject) => {
        getFunt.execute(function (response) {
            // console.log(response);
            resolve(response.result);
        });
    });
}

function trySetCredential() {
    return getConfigById(configFileId).then((data) => {
        // console.log(data);
        if (checkConfig(data)) {
            _isRegistered = false;
            return false;
        }
        publicKeyBase64 = data.publicKey;
        privateKeyBase64 = data.secretKey;
        publicKeyBuff = Buffer.from(publicKeyBase64, 'base64');
        privateKeyBuff = Buffer.from(privateKeyBase64, 'base64');
        userInfo.username = data.username;
        userInfo.userAddr = data.userAddr;
        userInfo.createTime = data.createTime;
        userInfo.publicKeyBuff = publicKeyBuff;
        _isRegistered = true;
        return true;
    });
}

function createConfig() {
    const createFunc = gapi.client.drive.files.create({
        'resource': {'name': configFileName, 'parents': ['appDataFolder']},
        'fields': 'id'
    });
    console.log('create config...');
    return new Promise((resolve, reject) => {
        createFunc.execute((response) => {
            // console.log(response);
            resolve(response.id);
        });
    });
}

function uploadConfig(id, jsonStr) {
    const req = gapi.client.request({
        'path': '/upload/drive/v3/files/' + id,
        'method': 'PATCH',
        'params': {'uploadType': 'media'},
        'body': jsonStr
    });
    console.log('upload config...');
    return new Promise((resolve, reject) => {
        req.execute((response) => {
            // console.log(response);
            resolve(response);
        });
    });
}

async function register(username, keyPair, createTime, userAddr) {
    const id = configFileId || await createConfig();
    // const keyPair = createKeyPair();
    publicKeyBuff = Buffer.from(keyPair.publicKey);
    privateKeyBuff = Buffer.from(keyPair.secretKey);
    publicKeyBase64 = publicKeyBuff.toString('base64');
    privateKeyBase64 = privateKeyBuff.toString('base64');
    userInfo.username = username;
    userInfo.userAddr = userAddr;
    userInfo.createTime = createTime;
    userInfo.publicKeyBuff = publicKeyBuff;
    await uploadConfig(id, {
        'username': username,
        'publicKey': publicKeyBase64,
        'secretKey': privateKeyBase64,
        'createTime': createTime,
        'userAddr': userAddr
    });
    configFileId = id;
}

async function userPrepare() {
    const id = await getConfigId();
    // console.log(id);
    if (!id) {
        _isRegistered = false;
        return false;
    }
    configFileId = id;
    return trySetCredential();
}

function isRegistered() {
    return _isRegistered;
}

function getUserInfo() {
    return userInfo;
}

function clearState() {
    configFileId = '';
    userInfo = {
        username: '',
        userAddr: '',
        createTime: '',
        publicKeyBuff: null
    };
    publicKeyBuff = null;
    privateKeyBuff = null;
    publicKeyBase64 = '';
    privateKeyBase64 = '';
    _isRegistered = false;
}

export default {
    init,
    isSignedIn,
    isAuthorized,
    handleAuthClick,
    GoogleAuth,
    userPrepare,
    createKeyPair,
    trySetCredential,
    register,
    signSignature,
    isRegistered,
    getUserInfo,
    clearState
};