import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {inject, observer} from 'mobx-react';


import {Button, withStyles} from "@material-ui/core";
import Snackbar from '@material-ui/core/Snackbar';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';

const styles = theme => ({
    close: {
        padding: theme.spacing.unit / 2,
    },
    snackbar: {
        top: theme.spacing.unit
    }
});


@inject('notificationSnackState')
@observer
class NotificationSnack extends Component {
    render() {
        const {classes, notificationSnackState} = this.props;
        return (
            <Snackbar
                autoHideDuration={5000}
                className={classes.snackbar}
                anchorOrigin={{vertical: 'top', horizontal: 'right'}}
                open={notificationSnackState.open}
                onClose={notificationSnackState.handleClose}
                ContentProps={{
                    'aria-describedby': 'message-id',
                }}
                message={notificationSnackState.message}
                action={[
                    <Button key="gotit" color="secondary" size="small" onClick={notificationSnackState.handleClose}>
                        Got It!
                    </Button>,
                    <IconButton
                        key="close"
                        aria-label="Close"
                        color="inherit"
                        className={classes.close}
                        onClick={notificationSnackState.handleClose}
                    >
                        <CloseIcon/>
                    </IconButton>,
                ]}
            />
        );
    }
}

export default withStyles(styles)(NotificationSnack);