import {observable, action, computed} from 'mobx';

class ThreadBodyState {
    @observable threadAddr = '';

    @action.bound setThreadAddr(threadAddr) {
        this.threadAddr = threadAddr;
    }

    @action.bound bindUpdateThreadBodyFunc(func) {
        this.updateThreadBodyFunc = func;
    }

    @action.bound callUpdateThreadBody() {
        if (this.updateThreadBodyFunc) this.updateThreadBodyFunc();
    }
}

export default ThreadBodyState;