import React from 'react';
import PropTypes from 'prop-types';
import {withRouter, Link, Redirect} from "react-router-dom";
import classNames from 'classnames';
import {inject, observer} from 'mobx-react';
import moment from 'moment';
import {Skeleton} from 'antd';

import {withStyles} from '@material-ui/core/styles'
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import TableCell from '@material-ui/core/TableCell';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import {AutoSizer, Column, SortDirection, Table} from 'react-virtualized';

import forumAjaxAdapter from '../Adapters/ForumAjaxAdapter';

const styles = theme => ({
    root: {},
    table: {
        fontFamily: theme.typography.fontFamily,
    },
    flexContainer: {
        display: 'flex',
        alignItems: 'center',
        boxSizing: 'border-box',
    },
    tableRow: {
        cursor: 'pointer',
    },
    tableRowHover: {
        '&:hover': {
            backgroundColor: theme.palette.grey[200],
        },
    },
    tableCell: {
        flex: 1,
    },
    noClick: {
        cursor: 'initial',
    },
    paper: {
        height: 'calc(100vh - 56px)',
        [`${theme.breakpoints.up('xs')} and (orientation: landscape)`]: {
            height: 'calc(100vh - 48px)',
        },
        [theme.breakpoints.up('sm')]: {
            height: 'calc(100vh - 64px)',
        },
        width: theme.spacing.unit * 140,
        maxWidth: '100%'
    }
});

class MuiVirtualizedTable extends React.PureComponent {

    componentDidMount() {

    }

    getRowClassName = ({index}) => {
        const {classes, rowClassName, onRowClick} = this.props;

        return classNames(classes.tableRow, classes.flexContainer, rowClassName, {
            [classes.tableRowHover]: index !== -1 && onRowClick != null,
        });
    };

    cellRenderer = ({cellData, columnIndex = null}) => {
        const {columns, classes, rowHeight, onRowClick} = this.props;
        const dataOrSkeleton = this.props.loadSuccess ? cellData : (
            <Skeleton active title={{width: '100%'}} paragraph={{rows: 0, marginTop: 0}}/>);
        return (
            <TableCell
                component="div"
                className={classNames(classes.tableCell, classes.flexContainer, {
                    [classes.noClick]: onRowClick == null,
                })}
                variant="body"
                style={{height: rowHeight}}
                align={(columnIndex != null && columns[columnIndex].numeric) || false ? 'right' : 'left'}
            >
                {/*{cellData}*/}
                {/*<Skeleton active title={{width: '100%'}} paragraph={{rows: 0, marginTop: 0}}/>*/}
                {dataOrSkeleton}
            </TableCell>
        );
    };

    headerRenderer = ({label, columnIndex, dataKey, sortBy, sortDirection}) => {
        const {headerHeight, columns, classes, sort} = this.props;
        const direction = {
            [SortDirection.ASC]: 'asc',
            [SortDirection.DESC]: 'desc',
        };

        const inner =
            !columns[columnIndex].disableSort && sort != null ? (
                <TableSortLabel active={dataKey === sortBy} direction={direction[sortDirection]}>
                    {label}
                </TableSortLabel>
            ) : (
                label
            );

        return (
            <TableCell
                component="div"
                className={classNames(classes.tableCell, classes.flexContainer, classes.noClick)}
                variant="head"
                style={{height: headerHeight}}
                align={columns[columnIndex].numeric || false ? 'right' : 'left'}
            >
                {inner}
            </TableCell>
        );
    };

    render() {
        const {classes, columns, ...tableProps} = this.props;
        return (
            <AutoSizer>
                {({height, width}) => (
                    <Table
                        className={classes.table}
                        height={height}
                        width={width}
                        {...tableProps}
                        rowClassName={this.getRowClassName}
                    >


                        {columns.map(({cellContentRenderer = null, className, dataKey, ...other}, index) => {
                            let renderer;
                            if (cellContentRenderer != null) {
                                renderer = cellRendererProps =>
                                    this.cellRenderer({
                                        cellData: cellContentRenderer(cellRendererProps),
                                        columnIndex: index,
                                    });
                            } else {
                                renderer = this.cellRenderer;
                            }

                            return (
                                <Column
                                    key={dataKey}
                                    headerRenderer={headerProps =>
                                        this.headerRenderer({
                                            ...headerProps,
                                            columnIndex: index,
                                        })
                                    }
                                    className={classNames(classes.flexContainer, className)}
                                    cellRenderer={renderer}
                                    dataKey={dataKey}
                                    {...other}
                                />
                            );
                        })}

                    </Table>
                )}
            </AutoSizer>
        );
    }
}

MuiVirtualizedTable.propTypes = {
    classes: PropTypes.object.isRequired,
    columns: PropTypes.arrayOf(
        PropTypes.shape({
            cellContentRenderer: PropTypes.func,
            dataKey: PropTypes.string.isRequired,
            width: PropTypes.number.isRequired,
        }),
    ).isRequired,
    headerHeight: PropTypes.number,
    onRowClick: PropTypes.func,
    rowClassName: PropTypes.string,
    rowHeight: PropTypes.oneOfType([PropTypes.number, PropTypes.func]),
    sort: PropTypes.func,
};

MuiVirtualizedTable.defaultProps = {
    headerHeight: 56,
    rowHeight: 56,
};

const WrappedVirtualizedTable = withStyles(styles)(MuiVirtualizedTable);

@inject('navState', 'threadListState')
class ReactVirtualizedTable extends React.Component {
    constructor(props) {
        super(props);
        props.threadListState.bindUpdateThreadListFunc(this.updateThreadList);
    }

    state = {
        loadSuccess: false,
        rows: Array(10).fill('')
    };

    updateThreadList = () => {
        const {createReplyBtnState} = this.props.navState;
        if (createReplyBtnState === 'add') {
            this.setState({loadSuccess: false, rows: Array(10).fill('')});
            const timeout = new Promise(resolve => setTimeout(resolve, 200));
            Promise.all([timeout, forumAjaxAdapter.getRecentThreads()]).then((res) => {
                res[1].map((val) => {
                    val.requestTime = moment(parseInt(val.requestTime)).calendar();
                    return val;
                });
                this.setState({loadSuccess: true, rows: res[1]});
            });
        }
    };

    componentWillMount() {

    }

    componentDidMount() {
        const navState = this.props.navState;
        navState.setIndexTitleMode(true);
        navState.updateNavTitle('Threads');
        navState.setCreateBtnState();
        this.updateThreadList();
    }

    componentWillUnmount() {
        const navState = this.props.navState;
        navState.updateNavTitle();
        navState.clearCreateReplyBtnState();
    }

    render() {
        const {classes} = this.props;
        const rows = this.state.rows;
        return (
            <Grid container justify="center" alignItems="center">
                <Paper elevation={24} square className={classes.paper}>
                    {/*<Skeleton active title={true} paragraph={{ rows: 0 }}/>*/}
                    <WrappedVirtualizedTable
                        loadSuccess={this.state.loadSuccess}
                        rowCount={rows.length}
                        rowGetter={({index}) => rows[index]}
                        onRowClick={event => this.props.history.push(`/thread/${event.rowData.threadAddr}`)}
                        columns={[
                            {
                                // maxWidth:'100%',
                                width: 120,
                                flexGrow: 1.0,
                                label: 'Title',
                                dataKey: 'threadTitle',
                            },
                            {
                                width: 100,
                                label: 'Vote',
                                dataKey: 'totalVote',
                                numeric: true,
                            },
                            {
                                width: 100,
                                label: 'Replies',
                                dataKey: 'totalReplies',
                                numeric: true,
                            },
                            {
                                width: 100,
                                label: 'Author',
                                dataKey: 'authorName',
                            },
                            {
                                width: 150,
                                label: 'Time',
                                dataKey: 'requestTime',
                            },
                        ]}
                    />
                </Paper>
            </Grid>
        );
    }
}

export default withStyles(styles)(withRouter(ReactVirtualizedTable));